/*  */
.header{
    /* background-color: grey; */
    padding: 20px 15px;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
}

.container{
    max-width: 1140px;
    margin: 0 auto;
}

.row{
    display: flex;
    flex-wrap: wrap;
}

.justify-content-between{
    justify-content: space-between;
}

.header .logo a{
    display: inline-block;
    line-height: 36px;
    font-size: 24px;
    font-weight: 700;
    text-align: center;
    width: 40px;
    height: 40px;
    color :#fb839e;
    border-radius: 50%;
    border: 2px solid #fb839e;
    text-transform: uppercase;
}

.header .hamburger-btn{
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 3px 3px 3px #f0f0f0, -3px -3px 3px #d3d3d3;
}

.header .hamburger-btn:hover{
    box-shadow: 3px 3px 3px #d3d3d3 , -3px -3px 3px  #f0f0f0;
}

.header .hamburger-btn span{
    display: block;
    width: 16px;
    height: 2px;
    background-color: grey;
    position: relative;
}

.header .hamburger-btn span:before,
.header .hamburger-btn span:after{
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: grey;
}

.header .hamburger-btn span:before{
    top: -6px;
}

.header .hamburger-btn span:after{
    top: 6px;
}

.outer-shadow{
    box-shadow: 3px 3px 3px #f8f8f8, -3px -3px 3px #d3d3d3;
}

/* navigation */
.nav-menu{
    position: fixed;
    background-color: #eff0f4;;
    padding: 0 15px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    overflow-y: auto;
    transition: all 0.6s linear;
    /* animation: flipleft 0.6s linear step-start ; */
}
.nav-menu .close-nav-menu{
    display: block;
    position: absolute;
    right:15px;
    top:10px;
  
    border-radius: 50%;
    cursor: pointer;
    font-size: 35px;
    text-align: center;
    color: grey;

    width: 40px;
    height: 40px;
    line-height: 35px;
    box-shadow:3px 3px 3px #f0f0f0, -3px -3px 3px #d3d3d3;
}

.nav-menu .close-nav-menu:hover{
    transition: all 0.6s ease;
    box-shadow: 3px 3px 3px #d3d3d3 , -3px -3px 3px  #f0f0f0;
    -webkit-animation: spin 3s linear infinite;
            animation: spin 3s linear infinite;
}

@-webkit-keyframes spin{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes spin{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.nav-menu-inner{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-menu ul{
    padding: 15px;
}

.nav-menu ul li{
    display: block;
    margin-bottom: 20px;
    text-align: center;
}

.nav-menu ul li:last-child{
    margin-bottom: 0;
}

.nav-menu ul li a{
    display: inline-block;
    font-size: 28px;
    font-weight: 400;
    color: grey;
    padding: 5px 30px;
    text-transform: capitalize;
    border-radius: 30px;
}

.nav-menu ul li a:hover{
    box-shadow: 3px 3px 3px #d3d3d3 , -3px -3px 3px  #f0f0f0 ;
}

.nav-menu .copyright-text{
    position: absolute;
    top: 50%;
    left: 0;
    font-size: 15px;
    color: grey;
    transform: translateY(-50%) rotate(-90deg);
}

.onclose{
    /* animation: flipleft;
    animation-duration: 2s;
    animation-timing-function: linear; */
    opacity: 0;
    visibility: hidden;
    transition: all 0.6s linear;
}

@-webkit-keyframes flipleft{
    0%{
        transform: translateX(-100%);
    }
    100%{
        transform: translateX(0);
    }
}

@keyframes flipleft{
    0%{
        transform: translateX(-100%);
    }
    100%{
        transform: translateX(0);
    }
}

