.home-section{
    position: relative;
}

.home-section .full-screen{
    padding: 50px 0;
    min-height: 100vh;
}

.section-title{
    /* background-color: red; */
    padding: 0 15px;
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
}
.section-title h2{
    display: inline-block;
    font-size: 30px;
    color: #7e737383;
    text-transform: uppercase;
    font-weight: 600;
