}

.home-section .home-text h2{
    color: #251f1fab;
    font-size: 55px;
    font-weight: 700;
    margin: 0;
}

.home-section .home-text h1{
    color: #07050573;
    font-size: 20px;
    font-weight: 500;
    margin: 0;
    text-transform: capitalize;
}


.home-section .home-text .btn-1{
    margin-top: 30px;
}

.btn-1:hover{
    box-shadow: 3px 3px 3px #d3d3d3 , -3px -3px 3px  #f0f0f0 ;
}
.img-box{
    display: block;
    margin: auto;
    max-width: 350px;
    height: 350px;  
    border-radius: 50%;
    overflow: hidden;
}

.img-box img{
    width: 100%;
    height: 100%;
    
    object-fit: cover;
}

.btn-1{
    padding: 10px 25px;
    display: inline-block;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 500;
    color: #d8093dc0;
    background-color: transparent;
    cursor: pointer;
    border-radius: 30px;
    transition: all 0.6s ease;
