.effect-wrap{
    /* background-color: turquoise; */
    /* position: absolute;
    right: 10%;
    bottom: 10%;
    width: 100px;
    height: 100px;
    animation: flip 5s 0.6s  infinite linear; */
}



.effect-wrap .effect{
    position: absolute;
    z-index: -1; 
}

.effect-wrap .effect-1{
    width: 30px;
    height: 30px;
    border: 4px solid violet;
    border-radius: 2px;
    -webkit-animation: spin 5s ease-in-out infinite;
            animation: spin 5s ease-in-out infinite;
    right: 10%;
    bottom: 10%;
}

@-webkit-keyframes spin{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes spin{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.effect-wrap .effect-3{
    width: 180px;
    height: 180px;
    border: 20px solid #c48282ab;
    border-radius: 50%;
    -webkit-animation: flip 2.5s linear infinite;
            animation: flip 2.5s linear infinite;
    right: 50%;
    top: -120px;
}

@-webkit-keyframes flip{
    0%,100%{
        transform: translateX(0px);
    }
    50%{
        transform: translateX(50px);
    }
}

@keyframes flip{
    0%,100%{
        transform: translateX(0px);
    }
    50%{
        transform: translateX(50px);
    }
}

.effect-wrap .effect-4{
   border-top: 30px solid transparent;
   border-left: 30px solid #06d79c;
   left: 30%;
   top:20%;
   -webkit-animation: spin 10s linear infinite;
           animation: spin 10s linear infinite;
}

.effect-wrap .effect-4:before{
    content: '';
    border-top: 30px solid transparent;
    border-left: 30px solid #06d79c;
    opacity: 0.5;
    position: absolute;
    left: -35px;
    top:-27px;
}

.effect-wrap .effect-5 {
    width: 50px;
    height: 50px;
   
    border-radius: 50%;
    right: 10%;
    top:50%;

    overflow: hidden;

    display: flex;
    justify-content: space-between;
    /* flex-direction: column; */
    -webkit-animation: flip 10s linear infinite;
            animation: flip 10s linear infinite;
   
}

.effect-wrap .effect-5 div{
    width: 1px;
    /* height: 100%; */
    -webkit-animation: spin 10s linear infinite;
            animation: spin 10s linear infinite;
    background-color: turquoise;
    opacity: 0.8;
 }

 /* about section */

.about-section{
    /* background-color: red; */
    padding: 80px 0 80px;
    min-height: 100vh;
}

.about-section .about-img{
    flex: 0 0 40%;
    max-width: 40%;
    padding: 0 15px;
}

.about-section .about-img .social-links{
    margin-top: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    
}

.about-section .about-img .social-links a{
    color:#251f1fab;
    height: 40px;
    line-height: 40px;
    width: 40px;
    /* padding: 0 10px; */
    border-radius: 50%;
    transition: all 0.6s linear;
}

.about-section .about-img .social-links a:hover{
    box-shadow: 3px 3px 3px #d3d3d3 , -3px -3px 3px  #f0f0f0 ;
    color: #d8093dc0;
}

.about-section .about-info{
    flex: 0 0 60%;
    max-width: 60%;
    padding: 0 15px;
}

.about-section .about-info span{
    font-weight: 600;
}

.about-section .about-info p{
    font-size: 16px;
    line-height: 26px;
    margin-bottom: 15px;
    color: #251f1fab;
}

.about-section .about-tags{
    padding: 60px 15px 50px;
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
}

.about-section .about-tags .tag-item{
   display: inline-block;
   margin: 0px 5px 10px;
   cursor: pointer;
   color: #7e737383;
   /* border: 1px solid red; */
   padding: 5px 10px;
   border-radius: 30px;
   transition: all 0.3s ease;
   font-size: 16px;
   text-transform: uppercase;
}

.about-section .about-tags .tag-item:hover{
    box-shadow: 3px 3px 3px #d3d3d3 , -3px -3px 3px  #f0f0f0 ;
}

.about-section .tag-content{
    flex: 0 0 100%;
    max-width: 100%;
    
    display: none;
}

.about-section .skills {
    padding: 0 0 40px;
}

.about-section .tag-content.active{
    -webkit-animation: fadeOut 0.6s ease;
            animation: fadeOut 0.6s ease;
    display: block ;
}

@-webkit-keyframes fadeOut{
    0%{
        opacity: 0;
        transform: translateY(-50px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeOut{
    0%{
        opacity: 0;
        transform: translateY(-50px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}

.about-section .skills .skill-item{
    flex: 0 0 50%;
    max-width: 50%;
    padding: 0 15px;
    margin-bottom: 40px;
}

.about-section .skills .skill-item p{
    font-size: 16px;
    color: #7e737383;
    text-transform: capitalize;
    margin: 0 0 10px;
}

.about-section .skills .skill-item .progress{
    height: 20px;
    border-radius: 10px;
    position: relative;
}

.about-section .skills .skill-item .progress-bar{
    background-color: #3d343483;
    height: 6px;
    left: 7px;
    top: 7px;
    position: absolute;
    border-radius: 10px;
}

.about-section .skills .skill-item .progress-bar span{
    position: absolute;
    right: 0;
    top: 0;
    margin-top: -40px;
    color: #3d343483;
}

.about-section .education,
.about-section .experience{
    padding-bottom: 80px;
}

.about-section .timeline{
    flex: 0 0 100%;
    max-width: 100%;
    position: relative;
}

.about-section .timeline:before{
    content: '';
    width: 1px;
    height: 100%;
    background-color: #7e737383;
    position: absolute;
    left: 50%;
    top: 0;
}

.about-section .timeline .timeline-item{
    flex: 0 0 100%;
    max-width: 100%;
}

.about-section .timeline .timeline-item:nth-child(odd){
    padding-right: calc(50% + 50px);
}
.about-section .timeline .timeline-item:nth-child(even){
    padding-left: calc(50% + 50px);
}

.about-section .timeline .timeline-item-inner{
    padding: 30px;
    border-radius: 5px;
    position: relative;
}

.about-section .timeline .timeline-item-inner .icon{
    height: 40px;
    line-height: 40px;
    width: 40px;
    border-radius: 50%;
    font-size: 16px;
    color: #d8093dc0;
    background-color: #c4828291;
    text-align: center;
    position: absolute;
    top: 10px;
}

.about-section .timeline .timeline-item:nth-child(odd) .icon{
    right: -70px;
}

.about-section .timeline .timeline-item:nth-child(even) .icon{
    left: -70px;
}

.about-section .timeline .timeline-item-inner span{
    font-weight: 500;
    color: #d8093dc0;
    display: block;
    margin: 0 0 10px;
    text-transform: capitalize;
}

.about-section .timeline .timeline-item-inner p{
    font-weight: 400;
    font-size: 16px;
    color:#7e737383;
    display: block;
    margin: 15px 0 ;
    /* text-transform: capitalize; */
}

.about-section .timeline .timeline-item-inner h3{
    font-weight: 600;
    font-size: 20px;
    color:#2c232383;
    display: block;
    margin: 0 0 5px;
    text-transform: capitalize;
}

.about-section .timeline .timeline-item-inner h4{
    font-weight: 600;
    font-size: 16px;
    color:#7e737383;
    display: block;
    margin: 0 ;
    text-transform: capitalize;
}

.ml-10{
    margin-right: 10px;
}
.contact{
    text-align: center;
    font-family: 'Roboto', sans-serif;
    
}
